















































































.list-wrapper {
  height: 372px;

  .active {
    @apply bg-brand-green text-white;
  }
}

.list-item {
  transition: all .3s ease;
  opacity: .45;
  @apply bg-grey-light;

  .icon-down {
    transition: all .3s ease;
  }

  &.active {
    @apply bg-white opacity-100;

    .icon-down {
      transform: rotate(-90deg) !important;
    }
  }
}
